.LoginScreen{
    position: fixed;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background: linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.5) ), url(/playTimee/static/media/ParkBasketball.4f097d08.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.LoginScreen h1, .LoginScreen h2 {
    text-align: center;
    border-radius: 5px;
    font-weight: bold;
}

.LoginScreen h2{
    margin-bottom: 30px;
}

.LoginScreen form{
    display: flex;
    flex-direction: column;
}
.LoginScreen input{
    border: 0;
    height: 20px;
    border-radius: 2px;
    margin: 5px;
    background-color: rgba(256, 256, 256, .4);
    color: white;
}
    .LoginScreen input, select, textarea{
        color: white;
    }


.loginbutton{
    display: flex;
    background-color: rgb(235, 188, 118);
    border-radius: 2px;
    padding: 5px;
    border: none;
    justify-content: center;
}
a:active{
    text-decoration: none;
}

@media only screen and (max-width: 700px) {
    .LoginScreen{
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0, 10px;
        background: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5) ), url(/playTimee/static/media/smallscreenlogin.d7e1d536.jpeg);
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-family: 'Roboto', sans-serif;
        color: white;
    }

    .LoginScreen h2 {
        text-align: center;
        border-radius: 5px;
        font-size: 1rem;
        margin: 0 10px;
    }

}
