.search {
    position: absolute;
    top: 1rem;
    left: 60%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 300px;
    z-index: 3;
}

.search input {
    padding: 0.5rem;
    font-size: 1.5rem;
    width: 100%;
  }

@media only screen and (max-width: 700px) {
    .search{
        left: 50%;
    }
}